<script setup lang="ts">
import Cookies from "js-cookie";

const { t } = useI18n();
const route = useRoute();
const productId = route.query.productId as string;
const redirectURL = route.query.redirectURL as string;
const title = route.query.title as string;

const parseDataFromRoute = () => {
  if (productId) {
    Cookies.remove(`transactionId_${productId}`);
  }
};

onMounted(parseDataFromRoute);
definePageMeta({ layout: "auth", middleware: "auth" });
</script>

<template>
  <AuthHeader transparent absolute />

  <BaseMain class="!h-[calc(100%-25px)]">
    <div class="flex px-4 items-center justify-center flex-col h-full">
      <div class="w-full max-w-[400px]">
        <div class="flex flex-col justify-center items-center">
          <div class="flex flex-col justify-center items-center gap-6">
            <div class="w-[64px] h-[64px] flex justify-center items-center rounded-full bg-[var(--base-success-color)]">
              <IconCheck :width="24" :height="24" fill="#ffff" />
            </div>

            <div class="flex flex-col justify-center items-center gap-4">
              <h1 class="text-3xl font-bold">
                {{ t('pages.events.appreciation') }}
              </h1>

              <p class="text-center text-[var(--base-secondary-color)] font-medium">
                {{ t('pages.events.success_purchased', { subscription: title || '' }) }}
              </p>
            </div>

            <BaseButton
              name="back to site"
              class="text-button-bold w-[12rem] text-center"
              tag="nuxt-link"
              :to="{ path: redirectURL || '/' }"
            >
              {{ t('labels.back_to_site') }}
            </BaseButton>
          </div>
        </div>
      </div>
    </div>
  </BaseMain>
</template>
